import { USER_API } from "@/api/client";
import { mapState } from "vuex";
// import activity from "@/components/activity";
import pupProtocol from './protocol.vue';
import trading from '@/components/trading';
export default {
  name: "trading",
  data() {
    return {
      status: false,
      show: false,
      isLoading: true,
      refreshLoading: false,
      activeType: 0,
      theme: 0,
      activeList: [this.$t('trading.Live'), this.$t('trading.Upcoming'), this.$t('trading.Past')],
      tradingList: [
          {}
      ]
    };
  },
  computed: {
    ...mapState(["local"]),
    locale() {
      return this.local.locale;
    },
    isBitKeep() {
      return this.local.UA.isBitKeep;
    },
  },
  components: {
    // activityCom: activity,
    pupProtocol,
    tradingCom: trading
  },
  
  mounted() {
    this.getInfo();
    this.setIcon();
  },
  methods: {
    // 获取信息
    async getInfo() {
      const { data, status } = await USER_API.miningInfo();
      if (status == 1) {
        this.isLoading = false;
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("staking.know"),
          confirmButtonColor: "$theme-light-colorPrimary",
        });
      }
      this.isLoading = false;
      this.refreshLoading = false;
    },
    inviteRewards(){
      this.$router.push('/activity/blindboxInvite/rewardList')
    },
    tabChange(val) {
      this.activeType = val;
    },
    setIcon(){
        this.isBitKeep &&
          BitKeepInvoke.onLoadReady(() => {
            BitKeepInvoke.setTitle(
              this.$t("trading.tradingTitle")
            );
          //设置主题
            this.$nextTick(() => {
              BitKeepInvoke.appMode((err, res) => {
                let body = document.getElementsByTagName("body")[0];
                if (res == 1) {
                  this.theme = 1;
                  body.setAttribute("class", "theme-dark");
                  setTimeout(()=>{
                    BitKeepInvoke.setIconAction(
                    "https://cdn.bitkeep.vip/u_b_09035ca0-4dd9-11ec-a555-07d5354e6fab.png",
                    ()=>{
                      this.$router.push("/activity/trading/history")
                    });
                  },500)
                } else {
                  this.theme = 0;
                  body.setAttribute("class", "theme-light");
                  setTimeout(()=>{
                    BitKeepInvoke.setIconAction(
                    "https://cdn.bitkeep.vip/u_b_2bb4fa20-3b86-11ec-8e63-1db435df936c.png",
                    ()=>{
                      this.$router.push("/activity/trading/history")
                    });
                  },500)
                }
              });
          })
        });
    },
    milliFormat(num) {
      return (
        num &&
        num
          .toString()
          .replace(/^\d+/g, (m) => m.replace(/(?=(?!^)(\d{3})+$)/g, ","))
      );
    },
    claim(){
      this.show = true;
    },
    close(val){
      if(val) {
        setTimeout(()=>{
          this.getInfo();
        },500)
      }
      this.show = false;
    },
    swap(){
      BitKeepInvoke.nativeApp({
          symbol0: {
            symbol: 'CAKE',
            chain: 'CAKE',
            chainName: 'CAKE',
            contract: 'contract',
            icon:  '123'
            },
            symbol1: {
            symbol: 'BNB',
            chainName: 'BNB',
            chain: 'BNB',
            contract: 'contract',
            icon: '123'
            }
      });
    },
    learnMore() {
      this.$router.push("/activity/mining/miningRule");
    },
  },
};
